.UpgradesCard {
  height: 100px;

  &__upgrade {
    position: relative;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    cursor: pointer;

    &_image {
      position: absolute;
      height: 100%;
    }

    &_coin {
      position: absolute;
      z-index: 2;
      height: 16px;
      bottom: 0;
      right: 0;
    }
  }
}
